import React, { useState } from "react";

function Contact() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    message: "",
  });

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await fetch("https://contact-backend-aent.onrender.com/send", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });

      const result = await response.json();
      if (response.ok) {
        alert("Message sent successfully!");
        setFormData({ name: "", email: "", message: "" }); // Clear the form
      } else {
        alert(`Error: ${result.error}`);
      }
    } catch (error) {
      console.error("Error:", error);
      alert("An error occurred. Please try again.");
    }
  };

  return (
    <section id="contact" style={styles.section}>
      <h2 style={styles.heading}>Contact Me</h2>
      <p>
        You can reach me at <strong>manshor.sadat@gmail.com</strong> or use the form below:
      </p>

      <form onSubmit={handleSubmit} style={styles.form}>
        <input
          type="text"
          name="name"
          placeholder="Your Name"
          value={formData.name}
          onChange={handleChange}
          required
          style={styles.input}
        />
        <input
          type="email"
          name="email"
          placeholder="Your Email"
          value={formData.email}
          onChange={handleChange}
          required
          style={styles.input}
        />
        <textarea
          name="message"
          placeholder="Your Message"
          value={formData.message}
          onChange={handleChange}
          required
          style={styles.textarea}
        />
        <button type="submit" style={styles.button}>
          Send Message
        </button>
      </form>

      {/* Footer Section for Social Media */}
      <footer id="footer" style={styles.footer}>
        <h3 style={styles.footerTitle}>Connect with me</h3>
        <div style={styles.socialIcons}>
          <a href="https://www.linkedin.com/in/sayed-manshor-sadat-365246205/" target="_blank" rel="noopener noreferrer">
            <img src="https://cdn-icons-png.flaticon.com/512/174/174857.png" alt="LinkedIn" style={styles.icon} />
          </a>
          <a href="https://github.com/Manshor-Sadat" target="_blank" rel="noopener noreferrer">
            <img src="https://cdn-icons-png.flaticon.com/512/733/733553.png" alt="GitHub" style={styles.icon} />
          </a>
          <a href="https://www.instagram.com/manshor_sadat/" target="_blank" rel="noopener noreferrer">
            <img src="https://cdn-icons-png.flaticon.com/512/2111/2111463.png" alt="Instagram" style={styles.icon} />
          </a>
          <a href="mailto:manshor.sadat@gmail.com">
            <img src="https://cdn-icons-png.flaticon.com/512/732/732200.png" alt="Email" style={styles.icon} />
          </a>
        </div>
      </footer>
    </section>
  );
}

const styles = {
  section: {
    padding: "50px 20px",
    textAlign: "center",
    backgroundColor: "#f4f4f4",
  },
  heading: {
    marginBottom: "20px",
    fontSize: "28px",
    fontWeight: "bold",
    color: "#003366", // Dark blue
  },
  form: {
    display: "flex",
    flexDirection: "column",
    maxWidth: "400px",
    margin: "auto",
  },
  input: {
    padding: "10px",
    margin: "10px 0",
    border: "1px solid #ccc",
    borderRadius: "5px",
    fontSize: "16px",
  },
  textarea: {
    padding: "10px",
    margin: "10px 0",
    border: "1px solid #ccc",
    borderRadius: "5px",
    fontSize: "16px",
    height: "100px",
    resize: "none",
  },
  button: {
    padding: "10px",
    backgroundColor: "#003366", // Consistent dark blue
    color: "white",
    border: "none",
    borderRadius: "5px",
    cursor: "pointer",
    fontSize: "16px",
    marginTop: "10px",
  },
  footer: {
    marginTop: "50px",
    padding: "20px 0",
    backgroundColor: "#003366",
    color: "white",
    textAlign: "center",
  },
  footerTitle: {
    marginBottom: "10px",
    fontSize: "20px",
  },
  socialIcons: {
    display: "flex",
    justifyContent: "center",
    gap: "15px",
  },
  icon: {
    width: "30px",
    height: "30px",
    cursor: "pointer",
  },
};

export default Contact;
