import React, { useState, useEffect } from "react";

function Navbar() {
  const [activeSection, setActiveSection] = useState("");

  useEffect(() => {
    const handleScroll = () => {
      const sections = document.querySelectorAll("section");
      let scrollPosition = window.scrollY + 200; // Offset for sticky navbar height

      sections.forEach((section) => {
        const sectionTop = section.offsetTop;
        const sectionHeight = section.offsetHeight;
        const sectionId = section.getAttribute("id");

        if (
          scrollPosition >= sectionTop &&
          scrollPosition < sectionTop + sectionHeight
        ) {
          setActiveSection(sectionId);
        }
      });
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <nav style={styles.navbar}>
      <h2 style={styles.logo}>My Portfolio</h2>
      <ul style={styles.navLinks}>
        <li>
          <a
            href="#about"
            style={{
              ...styles.link,
              color: activeSection === "about" ? "#4dabf7" : "#ffffff",
            }}
          >
            About
          </a>
        </li>
        <li>
          <a
            href="#projects"
            style={{
              ...styles.link,
              color: activeSection === "projects" ? "#4dabf7" : "#ffffff",
            }}
          >
            Projects
          </a>
        </li>
        <li>
          <a
            href="#certifications"
            style={{
              ...styles.link,
              color: activeSection === "certifications" ? "#4dabf7" : "#ffffff",
            }}
          >
            Certifications
          </a>
        </li>
        <li>
          <a
            href="#contact"
            style={{
              ...styles.link,
              color: activeSection === "contact" ? "#4dabf7" : "#ffffff",
            }}
          >
            Contact
          </a>
        </li>
      </ul>
      <div style={styles.socialMedia}>
        <p style={styles.socialText}> Connect with me </p>
        <div style={styles.socialIcons}>
          <a
            href="https://www.linkedin.com/in/sayed-manshor-sadat-365246205/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img
              src="https://cdn-icons-png.flaticon.com/512/174/174857.png"
              alt="LinkedIn"
              style={styles.icon}
            />
          </a>
          <a
            href="https://github.com/Manshor-Sadat"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img
              src="https://cdn-icons-png.flaticon.com/512/733/733553.png"
              alt="GitHub"
              style={styles.icon}
            />
          </a>
          <a
            href="https://www.instagram.com/manshor_sadat/"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img
              src="https://cdn-icons-png.flaticon.com/512/2111/2111463.png"
              alt="Instagram"
              style={styles.icon}
            />
          </a>
          <a href="mailto:manshor.sadat@gmail.com">
            <img
              src="https://cdn-icons-png.flaticon.com/512/732/732200.png"
              alt="Email"
              style={styles.icon}
            />
          </a>
        </div>
      </div>
    </nav>
  );
}

const styles = {
  navbar: {
    position: "sticky",
    top: 0,
    zIndex: 1000,
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    padding: "15px 20px",
    background: "linear-gradient(90deg, #003366, #004080)", // Dark blue gradient
    color: "#ffffff", // White text
    boxShadow: "0px 4px 6px rgba(0, 0, 0, 0.2)",
    flexWrap: "wrap",
  },
  logo: {
    fontSize: "24px",
    fontWeight: "bold",
  },
  navLinks: {
    listStyle: "none",
    display: "flex",
    gap: "15px",
    justifyContent: "center",
    margin: 0,
    padding: 0,
    flexWrap: "wrap",
    flexGrow: 1,
  },
  link: {
    textDecoration: "none",
    fontSize: "16px",
    color: "#ffffff", // White text by default
    cursor: "pointer",
    transition: "color 0.3s",
  },
  socialMedia: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  socialText: {
    fontSize: "14px",
    color: "#ffffff", // White text
    marginBottom: "5px",
  },
  socialIcons: {
    display: "flex",
    gap: "10px",
  },
  icon: {
    width: "24px",
    height: "24px",
    cursor: "pointer",
    transition: "transform 0.3s",
  },
};

export default Navbar;
